  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #4e3d3d;
  color: #ffffff;
  font-family: Arial, Helvetica, sans-serif;
  height: 100vh;
}

.box {
  width: 800px;
  outline: 1px solid #ffffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
}

.textarea {
  width: 100%;
  height: 200px;
  outline: 1px solid #ffffff;
  margin-bottom: 50px;
  background-color: black;
  color: #ffffff;
  padding: 10px;
  margin: 10px;
  margin-bottom: 50px;
}

.keyboard {
  width: 825px;
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
  transition: 0.1s;
}

.button {
  height: 45px;
  width: 45px;
  margin: 5px;
  outline: 1px solid #ffffff;
  text-align: center;
  line-height: 2.5;
}

.clickStyle {
  outline: 4px solid #ffffff;
  transition: 0.1s;
